.sign__submit {
  width: 358px;
  height: 50px;
  cursor: pointer;
  font-family: 'Inter', 'Arial', 'Helvetica', sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 18px;
  line-height: 22px;
  text-align: center;
  color: #000000;
  border-radius: 2px;
  padding: 14px 0 14px 0;
  border: none;
  margin-bottom: 15px;
  transition: 0.3s;
}

.sign__submit:hover {
  opacity: 0.6;
}