.sign__move {
  font-family: 'Inter', 'Arial', 'Helvetica', sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 17px;
  text-align: center;
  color: #FFFFFF;
  cursor: pointer;
  transition: 0.3s;
  text-decoration: none;
}

.sign__move:hover {
  opacity: 0.6;
}